import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginPage } from './pages/login/login.page';
import { WebSSOPage } from './pages/web-sso/web-sso.page';
import { MyGroupsPage } from './pages/my-groups/my-groups.page';
import { MessagesPage } from './pages/messages/messages.page';
import { ChatPage } from './pages/chat/chat.page';
import { SearchPage } from './pages/search/search.page';
import { CalendarPage } from './pages/calendar/calendar.page';
import { ProfilePage } from './pages/profile/profile.page';
import { SettingsPage } from './pages/settings/settings.page';
import { DeleteProfilePage} from './pages/delete-profile/delete-profile.page';


const routes: Routes = [
    {
        path: '',
        pathMatch: 'full',
        redirectTo: '/login'
    },
    {
        path: 'login',
        component: LoginPage
    },
    {
        path: 'web-sso',
        component: WebSSOPage,
    },
    {
        path: 'my-groups',
        component: MyGroupsPage
    },
    {
        path: 'messages',
        component: MessagesPage
    },
    {
        path: 'messages/:chatId',
        component: ChatPage
    },
    {
        path: 'search',
        component: SearchPage
    },
    {
        path: 'calendar',
        component: CalendarPage
    },
    {
        path: 'profile',
        component: ProfilePage
    },
    {
        path: 'settings',
        component: SettingsPage
    },
    {
      path: 'deleteProfile',
      component: DeleteProfilePage
    },
];

@NgModule({
    imports: [RouterModule.forRoot(routes, {useHash: true})],
    exports: [RouterModule],
})
export class AppRoutingModule {}
