import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'create-meeting-dialog',
  templateUrl: './create-meeting-dialog.component.html',
  styleUrls: ['./create-meeting-dialog.component.scss']
})
export class CreateMeetingDialog implements OnInit {

  public groupOptions: string[] = [
    "Analysis A",
    "Programmieren I",
  ];

  meetingName: string = "";
  meetingStart: Date = null;
  meetingEnd: Date = null;
  meetingGroup: string = "";

  constructor(private dialogRef: MatDialogRef<CreateMeetingDialog>, @Inject(MAT_DIALOG_DATA) dialogData) {}

  ngOnInit(): void {}

  onCreatePressed() : void {
    let meeting = {
      id: new Date().getTime(),
      title: this.meetingGroup + ": " + this.meetingName,
      start: this.meetingStart,
      end: new Date(this.meetingEnd.getTime() + 20),
      extendedProps: {
        editable: true,
      }
    };

    if(!this.meetingGroup || !this.meetingName || !this.meetingStart || !this.meetingEnd)
      meeting = null;

    this.dialogRef.close(meeting);
  }

}
