import { Component, Inject, OnInit } from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA, MatDialog} from '@angular/material/dialog';
import {GroupJoiningDialog} from '../group-joining-dialog/group-joining-dialog.component';
import {Constants} from "../../constants/constants";

@Component({
  selector: 'app-view-group-dialog',
  templateUrl: './view-group-dialog.component.html',
  styleUrls: ['./view-group-dialog.component.scss']
})
export class ViewGroupDialog implements OnInit {

  public readonly degreeCourseOptions: string[] = [
    "Informatik",
    "Maschinenbau",
    "Elektrotechnik",
    "Mathematik",
  ];

  public readonly semesterOptions: string[] = [
    "Semesterunabhngig",
    "SoSe 2021",
    "WiSe 2020",
    "SoSe 2020",
    "WiSe 2019",
    "SoSe 2019",
  ];

  groupObj: any;
  openedFromOwnGroups: boolean;
  newGroupDialog: boolean;
  preFilledJoiningMessage: string;

  degreeCourses: string;
  groupIsFull: boolean;
  groupJoiningIsNotPossible: boolean;
  toolTipStr: string;
  toolTipOptions: any;
  lockIcon: string;

  constructor(private dialog: MatDialog, private dialogRef: MatDialogRef<ViewGroupDialog>, @Inject(MAT_DIALOG_DATA) dialogData) {
    this.groupObj = dialogData.groupObj;
    this.openedFromOwnGroups = dialogData.openedFromOwnGroups;
    this.newGroupDialog = (dialogData.newGroupDialog === null) ? false : dialogData.newGroupDialog;
    this.preFilledJoiningMessage = (dialogData.preFilledJoiningMessage == null) ? '' : dialogData.preFilledJoiningMessage;
    this.lockIcon = dialogData.groupObj.isOpen ? 'lock_open' : 'lock';
    this.groupIsFull = dialogData.groupObj.size >= dialogData.groupObj.capacity;
    this.groupJoiningIsNotPossible = this.groupIsFull || this.groupObj.own;
    this.toolTipStr = this.groupIsFull ? 'Vollen Gruppen kann nicht beigetreten werden' : 'Du bist dieser Gruppe schon beigetreten';
    this.toolTipOptions = {
      display: this.groupJoiningIsNotPossible
    };

    this.degreeCourses = '';
    let firstObj = true;
    dialogData.groupObj.degreeCourses.forEach( (degreeCourse) => {
      if (!firstObj) {
        this.degreeCourses += ', ';
      }
      firstObj = false;
      this.degreeCourses += degreeCourse;
    });

    // If this dialog returns true -> remove the selected group or add a new group if newGroupDialog.
    dialogRef.beforeClosed().subscribe(() => dialogRef.close(false));

  }

  saveNewGroup(): void {
    // If this dialog returns true -> add the new group
    this.dialogRef.beforeClosed().subscribe(() => this.dialogRef.close(true));
    this.dialogRef.close();
  }

  updateGroupIsFull(): void {
    this.groupObj.capacityString = this.groupObj.capacity.toString();
    this.groupIsFull = this.groupObj.size >= this.groupObj.capacity;
  }

  changeGroupTitle(): void {
    const newTitle = prompt('Neuer Gruppen Titel: ', this.groupObj.name);
    if (newTitle == null) {
      return;
    }
    this.groupObj.name = newTitle;
  }

  toggleLockIcon(): void {
    if (this.lockIcon === 'lock') {
      this.lockIcon = 'lock_open';
      this.groupObj.isOpen = true;
    }
    else {
      this.lockIcon = 'lock';
      this.groupObj.isOpen = false;
    }
  }

  removeMember(member: string): void {
    let msg = "Mchtest du wirklich " + member + " aus der Gruppe \"" + this.groupObj.name + "\" hinauswerfen ?";
    if (member === "Jana Kurz") {
      msg = "Mchtest du wirklich " + this.groupObj.name + " verlassen ?";
    }
    const kickMember = confirm(msg);
    if (kickMember) {
      const index = this.groupObj.members.indexOf(member);
      this.groupObj.members.splice(index, 1);
      --this.groupObj.size;
      this.groupObj.sizeString = String(this.groupObj.size);
      this.groupIsFull = false;
      if (member === "Jana Kurz") {
        // If this dialog returns true -> remove the selected group.
        this.dialogRef.beforeClosed().subscribe(() => this.dialogRef.close(true));
        this.dialogRef.close();
      }
    }
  }

  onJoining(): void {
    this.dialogRef.close();
    this.dialog.open(GroupJoiningDialog, {
      minWidth: '450px',
      data: {
        groupObj: this.groupObj,
        preFilledJoiningMessage: this.preFilledJoiningMessage,
        dialogBefore: ViewGroupDialog // Circumvents circular dependency warning.
      }
    });
  }

  ngOnInit(): void {
  }

}
