import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';

import { Constants } from 'src/app/constants/constants';

@Component({
	selector: 'chat-page',
	templateUrl: './chat.page.html',
	styleUrls: ['./chat.page.scss']
})
export class ChatPage implements OnInit {

	public chat: any;
	public message: string;
	public name: string;

	ngOnInit(): void {
		this.scrollToBottom();
	}

	constructor(private route: ActivatedRoute) {
		this.route.params.subscribe((params: Params) => {
			this.chat = Constants.chats[params.chatId];
			this.name = Constants.username;
		});
	}
	
	public onMessageSubmitted(): void {
		if(!this.message || this.message.trim() == "") return;  
		let chatMessage = { author: this.name, text: this.message, };
		this.chat.messages.push(chatMessage);
		this.message = "";
		this.scrollToBottom();
	}
	
	public async scrollToBottom(): Promise<void> {
		await new Promise(resolve => setTimeout(resolve, 1));
		var element = document.getElementById("message-list");
		element.scrollTop = element.scrollHeight;
	}
	
}
