import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';

import { FullCalendarModule } from '@fullcalendar/angular';
import dayGridPlugin from '@fullcalendar/daygrid';

import { MatButtonModule } from '@angular/material/button';
import { MatDialogModule } from '@angular/material/dialog';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatListModule } from '@angular/material/list';
import { MatPaginatorModule } from '@angular/material/paginator';
import { MatSelectModule } from '@angular/material/select';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';
import { MatCardModule } from '@angular/material/card';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatNativeDateModule, MAT_DATE_LOCALE } from '@angular/material/core';

import { AppRoot } from './app.root';
import { AppRoutingModule } from './app-routing.module';

import { HeaderComponent } from './components/header/header.component';

import { LoginPage } from './pages/login/login.page';
import { WebSSOPage } from './pages/web-sso/web-sso.page';
import { MyGroupsPage } from './pages/my-groups/my-groups.page';
import { MessagesPage } from './pages/messages/messages.page';
import { ChatPage } from './pages/chat/chat.page';
import { SearchPage } from './pages/search/search.page';
import { CalendarPage } from './pages/calendar/calendar.page';
import { ProfilePage } from './pages/profile/profile.page';
import { SettingsPage } from './pages/settings/settings.page';
import { DeleteProfilePage } from './pages/delete-profile/delete-profile.page';

import { CreateMeetingDialog } from './dialogs/create-meeting-dialog/create-meeting-dialog.component';
import { EditMeetingDialog } from './dialogs/edit-meeting-dialog/edit-meeting-dialog.component';
import { ViewMeetingDialog } from './dialogs/view-meeting-dialog/view-meeting-dialog.component';
import { ViewGroupDialog } from './dialogs/view-group-dialog/view-group-dialog.component';

import { GroupJoiningDialog } from './dialogs/group-joining-dialog/group-joining-dialog.component';
import { ToastrModule } from 'ngx-toastr';
import { DeleteProfileDialog } from './dialogs/delete-profile-dialog/delete-profile-dialog.component';

FullCalendarModule.registerPlugins([
    dayGridPlugin,
]);

@NgModule({
    declarations: [
        AppRoot,
        HeaderComponent,

        LoginPage,
        WebSSOPage,
        MyGroupsPage,
        MessagesPage,
        ChatPage,
        SearchPage,
        CalendarPage,
        ProfilePage,
        SettingsPage,

        CreateMeetingDialog,
        EditMeetingDialog,
        ViewMeetingDialog,
        ViewGroupDialog,
        GroupJoiningDialog,
        DeleteProfilePage,
        DeleteProfileDialog
    ],
    imports: [
        BrowserModule,
        AppRoutingModule,
        BrowserAnimationsModule,
        FormsModule,
        ReactiveFormsModule,

        FullCalendarModule,

        MatButtonModule,
        MatDialogModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatListModule,
        MatPaginatorModule,
        MatSelectModule,
        MatSidenavModule,
        MatToolbarModule,
        MatSlideToggleModule,
        MatCheckboxModule,
        MatCardModule,
        MatDatepickerModule,
        MatNativeDateModule,
        ToastrModule.forRoot(), // ToastrModule added
        MatTooltipModule,
    ],
    providers: [
        { provide: MAT_DATE_LOCALE, useValue: 'de-DE' }
    ],
    bootstrap: [AppRoot],
})
export class AppModule {}
