import { Component } from '@angular/core';
import {ViewGroupDialog} from "../../dialogs/view-group-dialog/view-group-dialog.component";
import {MatDialog} from "@angular/material/dialog";
import {Constants} from "../../constants/constants";

@Component({
  selector: 'my-groups-page',
  templateUrl: './my-groups.page.html',
  styleUrls: ['./my-groups.page.scss']
})
export class MyGroupsPage {

  public readonly groups: any[] = [
    {
      name: "Analysis A",
      description: "Lorem ipsum dolor sit amet consetetur sadipscing elitr",
      capacity: 5,
      capacityString: "5",
      size: 3,
      sizeString: "3",
      degreeCourses: [
        "Informatik",
        "Maschinenbau",
        "Mathematik",
      ],
      semester: "WiSe 2019",
      members: [
        "John",
        "Abraham",
        "Jana Kurz",
      ],
      own: true,
      chatId: 0,
      isOpen: true
    },
    {
      name: "Programmieren I",
      description: "Lorem ipsum dolor sit amet consetetur sadipscing elitr",
      capacity: 2,
      capacityString: "2",
      size: 2,
      sizeString: "2",
      degreeCourses: [
        "Informatik",
        "Elektrotechnik",
      ],
      semester: "WiSe 2020",
      members: [
        "Bart",
        "Jana Kurz",
      ],
      own: true,
      chatId: 3,
      isOpen: true
    },
    {
      name: "Klausur GRA",
      description: "Lorem ipsum dolor sit amet consetetur sadipscing elitr",
      capacity: 300,
      capacityString: "300",
      size: 6,
      sizeString: "6",
      degreeCourses: [
        "Informatik",
        "Elektrotechnik",
        "Maschinenbau",
      ],
      semester: "SoSe 2020",
      members: [
        "Angela",
        "Mark",
        "Elon",
        "Donald",
        "Jana Kurz",
        "Sami",
      ],
      own: true,
      chatId: 2,
      isOpen: true
    }
  ];

  public readonly degreeCourseOptions: string[] = [
    "Informatik",
    "Maschinenbau",
    "Elektrotechnik",
    "Mathematik",
  ];

  public readonly semesterOptions: string[] = [
    "Semesterunabhngig",
    "WiSe 2020",
    "SoSe 2020",
    "WiSe 2019",
    "SoSe 2019",
  ]

  public searchQuery: string = "";
  public degreeCourses: string[] = this.degreeCourseOptions;
  public semesters: string[] = this.semesterOptions;
  public minGroupSize: number = null;
  public maxGroupSize: number = null;

  public onGroupClicked(group: any): void {
    const openedDialog = this.dialog.open(ViewGroupDialog, {
      minWidth: '450px',
      data: {
        groupObj: group,
        openedFromOwnGroups: true
      }
    });
    // Remove selected group if the dialog returns true
    openedDialog.afterClosed().subscribe(result => {
      if (result) {
        const index = this.groups.indexOf(group);
        this.groups.splice(index, 1);
      }
    });
  }

  public addNewGroup(): void {
    const newGroup = {
      name: "Neue Gruppe",
      description: "Fge hier eine Beschreibung hinzu",
      capacity: 6,
      capacityString: "6",
      size: 1,
      sizeString: "1",
      degreeCourses: [
        "Informatik"
      ],
      semester: "WiSe 2020",
      members: [
        "Jana Kurz"
      ],
      own: true,
      chatId: 4,
      isOpen: true
    };

    const openedDialog = this.dialog.open(ViewGroupDialog, {
      minWidth: '450px',
      data: {
        groupObj: newGroup,
        openedFromOwnGroups: true,
        newGroupDialog: true
      }
    });

    openedDialog.afterClosed().subscribe(result => {
      if (result) {
        Constants.chats.push({
          id: Constants.idCount,
          name: newGroup.name,
          messages: [
            {
              author: Constants.username,
              text: 'Willkommen in ' + newGroup.name + '!',
            },
          ],
        });
        newGroup.chatId = Constants.idCount;
        this.groups.push(newGroup);
        ++Constants.idCount;
      }
    });
  }

  public get filteredGroups() : any[] {
    return this.groups.filter((group: any) => {
      // Filter by search query
      for(const value of Object.values(group)) {
        if(value.toString().toLowerCase().includes(this.searchQuery.toLowerCase()))
          return true;
      }
      return false;
    }).filter((group: any) => {
      // Filter by degree courses
      return this.degreeCourses.some(degreeCourse => group.degreeCourses.includes(degreeCourse));
    }).filter((group: any) => {
      // Filter by semester
      return this.semesters.includes(group.semester);
    }).filter((group: any) => {
      // Filter by minimum group size
      return this.minGroupSize != null ? group.size >= this.minGroupSize : true;
    }).filter((group: any) => {
      // Filter by maximum group size
      return this.maxGroupSize != null ? group.size <= this.maxGroupSize : true;
    });
  }
  constructor(private dialog: MatDialog) { }
}
