import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { DateAdapter } from '@angular/material/core';
import { MatDialog } from '@angular/material/dialog';

import { CalendarOptions, FullCalendarComponent } from '@fullcalendar/angular';
import { CreateMeetingDialog } from 'src/app/dialogs/create-meeting-dialog/create-meeting-dialog.component';
import { EditMeetingDialog } from 'src/app/dialogs/edit-meeting-dialog/edit-meeting-dialog.component';
import { ViewMeetingDialog } from 'src/app/dialogs/view-meeting-dialog/view-meeting-dialog.component';

@Component({
  selector: 'calendar-page',
  templateUrl: './calendar.page.html',
  styleUrls: ['./calendar.page.scss']
})
export class CalendarPage implements AfterViewInit, OnInit {

  public readonly MONTH_NAMES = [
    "Januar",
    "Februar",
    "Mrz",
    "April",
    "Mai",
    "Juni",
    "Juli",
    "August",
    "September",
    "Oktober",
    "November",
    "Dezember"
  ];

  public meetings: any[] = [
    {
      id: new Date().getTime(),
      title: "Analysis A: bungsblatt 3",
      start: new Date(new Date().getTime() + 2 * 24 * 60 * 60 * 1000),
      end: new Date(new Date().getTime() + 3 * 24 * 60 * 60 * 1000),
      extendedProps: {
        editable: true,
      }
    },
    {
      id: new Date().getTime(),
      title: "Klausur GRA: Gemeinsame Vorbereitung",
      start: new Date(new Date().getTime() + -4 * 24 * 60 * 60 * 1000),
      end: new Date(new Date().getTime() + -7 * 24 * 60 * 60 * 1000),
      extendedProps: {
        editable: false,
      }
    }
  ];

  public readonly calendarOptions: CalendarOptions = {
    initialView: "dayGridMonth",
    headerToolbar: false,
    height: "100%",
    locale: "de",
    firstDay: 1,
    displayEventTime: false,
    eventColor: "#3F51B5",
    eventClick: (event) => this.onEventClicked(event.event),
    dayHeaderFormat: {
      weekday: "long",
    },
    events: this.meetings,
  };

  @ViewChild(FullCalendarComponent)
  private calendar: FullCalendarComponent;

  public currentMonth: string = this.MONTH_NAMES[new Date().getMonth()];
  public currentYear: number = new Date().getFullYear();

  constructor(private dialog: MatDialog, private dateAdapter: DateAdapter<Date>) {}

  public ngOnInit() : void {
    this.dateAdapter.getFirstDayOfWeek = () => 1;
  }

  public ngAfterViewInit() : void {
    setTimeout(() => this.calendar.getApi().updateSize());
  }

  public onTodayClicked() : void {
    this.calendar.getApi().today();
    this.currentMonth = this.MONTH_NAMES[this.calendar.getApi().getDate().getMonth()];
    this.currentYear = this.calendar.getApi().getDate().getFullYear();
  }

  public onPreviousMonthClicked() : void {
    this.calendar.getApi().prev();
    this.currentMonth = this.MONTH_NAMES[this.calendar.getApi().getDate().getMonth()];
    this.currentYear = this.calendar.getApi().getDate().getFullYear();
  }

  public onNextMonthClicked() : void {
    this.calendar.getApi().next();
    this.currentMonth = this.MONTH_NAMES[this.calendar.getApi().getDate().getMonth()];
    this.currentYear = this.calendar.getApi().getDate().getFullYear();
  }

  public onAddMeetingClicked() : void {
    this.dialog.open(CreateMeetingDialog, {
      width: "400px",
    }).afterClosed().subscribe(newMeeting => {
      if(!newMeeting) return;
      this.meetings.push(newMeeting);
    });
  }

  public onEventClicked(event: any) : void {
    if(event.extendedProps.editable) {
      this.dialog.open(EditMeetingDialog, {
        width: "400px",
        data: {
          meetingId: event.id,
          meetingName: event.title.substring(event.title.indexOf(":") + 2),
          meetingStart: event.start,
          meetingEnd: event.end,
          meetingGroup: event.title.substring(0, event.title.indexOf(": ")),
        }
      }).afterClosed().subscribe(newMeeting => {
        if(!newMeeting) return;
        if(newMeeting.delete) {
          const meetingIndex = this.meetings.findIndex(x => x.id == event.id);
          this.meetings.splice(meetingIndex, 1)
          return;
        }
        const meetingIndex = this.meetings.findIndex(x => x.id == event.id);
        this.meetings[meetingIndex] = newMeeting;
      })
    } else {
      this.dialog.open(ViewMeetingDialog, {
        width: "400px",
        data: {
          meetingId: event.id,
          meetingName: event.title.substring(event.title.indexOf(":") + 2),
          meetingStart: event.start,
          meetingEnd: event.end,
          meetingGroup: event.title.substring(0, event.title.indexOf(": ")),
        }
      });
    }
    
  }
}
